html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-size: 16px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: normal;
  color: #333;
  /* ***** ***** ***** ***** ***** ***** ***** ***** ***** ***** */
  /* ***** ***** ***** *****   anchors   ***** ***** ***** ***** */
  /* ***** ***** ***** ***** ***** ***** ***** ***** ***** ***** */
  box-sizing: border-box;
}
html a {
  color: #0096d7;
  text-decoration: none;
}
html a:link,
html a:active,
html a:visited {
  color: #0096d7;
  text-decoration: none;
}
html a:hover {
  text-decoration: underline;
  color: #0096d7;
}
html a[name] {
  vertical-align: top;
}
html a[href*="eia.gov"]:after,
html .global-nav a[href*=".com"]:after,
html .global-nav a[href^="http:"]:after,
html .global-nav a[href^="https:"]:after,
html .social a:after,
html .social a[href^="https:"]:after,
html .bx-viewport a:after,
html .bx-viewport a[href^="https:"]:after {
  content: none;
  background: none;
}
html .downloadable-files a[href*=".com"]:after,
html .downloadable-files a[href^="http:"]:after,
html .downloadable-files a[href^="https:"]:after {
  margin: 0 8px 0 -6px;
}
html a[href^="tel:"] {
  color: #333;
  text-decoration: none;
}
html a[href^="tel:"]:hover {
  color: #333;
  text-decoration: none;
}
@media (max-width: 599px) {
  html a[href^="tel:"] {
    color: #0096d7;
    text-decoration: none;
  }
}
html * {
  box-sizing: border-box;
}
body {
  width: 100%;
  height: 100%;
}
.print-only {
  display: none;
}
.top {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: normal;
  color: #333;
  font-size: 0.75rem;
  text-align: left;
  font-weight: 700;
  display: flex;
  align-items: flex-end;
  margin: 2em 0 0 0;
  padding: 0 0 0 0;
}
/* ***** ***** ***** ***** ***** ***** ***** ***** ***** ***** */
/* ***** ***** ***** **** global reset ***** ***** ***** ***** */
/* ***** ***** ***** ***** ***** ***** ***** ***** ***** ***** */
.fancybox-overlay .ui-widget,
.fancybox-overlay .ui-state-default,
.fancybox-overlay .ui-widget-content .ui-state-default,
.fancybox-overlay .ui-state-active,
.fancybox-overlay .ui-widget-content .ui-state-active {
  border-width: 0px;
  background-color: transparent;
  background-image: none;
  outline: none;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-style: normal;
  color: #333;
}
.fancybox-overlay .ui-corner-left {
  -moz-border-radius-topleft: 0;
  -webkit-border-top-left-radius: 0;
  -moz-border-radius-bottomleft: 0;
  -webkit-border-bottom-left-radius: 0;
}
