/* ***** ***** ***** ***** base styles ***** ***** ***** ***** */
/* ***** ***** ***** ***** ***** ***** ***** ***** ***** ***** */
.print-version,
.hide {
  display: none;
}
/**
* 1. Add the correct box sizing in Firefox.
* 2. Show the overflow in Edge and IE.
*/
hr {
  overflow: visible;
  /* 2 */
  background: #d4d4d4;
  color: #d4d4d4;
  clear: both;
  float: none;
  width: 100%;
  height: 1px;
  margin: 1.45em 0;
  border: none;
}
hr.secondary {
  background: #0096d7;
  color: #0096d7;
}
.no-border {
  border-color: transparent;
  border-top: none;
  border-right: none;
  border-bottom: none !important;
  border-left: none;
}
/* ***** ***** ***** ***** ***** ***** ***** ***** ***** ***** */
/* ***** ***** ***** *** force line-break ** ***** ***** ***** */
/* ***** ***** ***** ***** ***** ***** ***** ***** ***** ***** */
