  display: inline-block;
}
span.highlight {
  background: #8ce44b;
}
/* ***** ***** ***** ***** ***** ***** ***** ***** ***** ***** */
/* ***** ***** ***** **** responsiveness *** ***** ***** ***** */
/* ***** ***** ***** ***** ***** ***** ***** ***** ***** ***** */
.svg-container {
  overflow: hidden;
}
.svg-container,
.image-container {
  text-align: center;
  width: 100%;
  margin: 0 auto 1em auto;
}
.svg-container a,
.image-container a {
  width: 100%;
}
.svg-container a img,
.image-container a img {
  text-align: center;
  padding: 0;
  margin: 0 auto;
}
.svg-container svg,
.image-container svg {
  -webkit-box-flex: 0;
  -webkit-flex: 0 0 100%;
  -ms-flex: 0 0 100%;
  flex: 0 0 100%;
  max-width: 100%;
  width: 100%;
  padding: 0;
  margin: 0 auto;
  background-size: cover;
}
.responsive {
  -webkit-box-flex: 0;
  -webkit-flex: 0 0 100%;
  -ms-flex: 0 0 100%;
  flex: 0 0 100%;
  max-width: 100%;
  width: 100%;
  padding: 0;
  margin: 0 auto;
  background-size: cover;
}
.responsive.frame {
  padding: 4px;
  border: 1px solid #dadada;
  margin: 0px 18px 10px 0px;
}
.responsive-container {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: normal;
  color: #333;
  color: #858585;
  font-size: 12px;
}
.responsive-container .date {
  display: inline-block;
  font-weight: normal;
  font-style: normal;
  color: #333;
  color: #858585;
}
.responsive-container .label {
  display: inline-block;
  font-weight: 700;
  color: #858585;
}
